(function(angular) {
  'use strict';

  /**
   * @ngdoc service
   * @name ipMobile.CityHome
   * @description
   * # CityHome
   * Service in the ipMobile.
   */
  angular.module('yoophawebapp')
    .service('uploadService', uploadService);

  /** @ngInject */
  function uploadService($q, $localStorage, getData, postData, utilities) {

    var service = {};
    var url;
    service.createCart = createCart;
    service.getDrugInfo = getDrugInfo;

    function createCart(data){
      url = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/cart/create_cart/';
      var deferred = $q.defer();
      postData.async(url, data)
        .then(function(res) {
          if(res.status == 201 && res.statusText == 'Created')
            deferred.resolve(res.data);
          else 
            deferred.reject(res.data);
        });
      return deferred.promise;
    }

    function getSearchData(url){
      var deferred = $q.defer();
      getData.async(url)
        .then(function(res) {
          deferred.resolve(res);
        });
      return deferred.promise;
    }

    function getDrugInfo(key){
      url = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/product/medicine_search/?search=' + key;
      var deferred = $q.defer();
      getData.async(url)
        .then(function(res) {
          deferred.resolve(res);
        });
      return deferred.promise; 
    }

    return service;

  }
})(angular);
(function(angular) {
  'use strict';

  angular
    .module('yoophawebapp')
    .controller('uploadPrescriptionController', uploadPrescriptionController);

  /** @ngInject */
  function uploadPrescriptionController($rootScope, $scope, $http, $localStorage, $state, $uibModal, uploadService, utilities) {
    var vm = this;
    var imageListener;
    var slides = vm.slides = [];
    var currIndex = 0;
    var img = ['assets/images/img1.png', 'assets/images/img1.png', 'assets/images/img1.png', 'assets/images/img1.png', 'assets/images/img1.png', 'assets/images/img1.png'];
    vm.selectedImages = [];
    vm.drug_key = undefined;
    vm.drug_search_result = [];
    vm.isImageSelected = false;
    vm.myInterval = 4500;
    vm.active = 0;
    vm.orderAll = true;
    vm.selected_drug = [];
    vm.create_cart_presc = [];
    vm.prefill = {};
    vm.routeTo = routeTo;
    vm.prepUpload = prepUpload;
    vm.editSelected = editSelected;
    vm.search_drug = search_drug;
    vm.add_drug = add_drug;
    vm.del_selected_drug = del_selected_drug;
    vm.update_selected_drug_qty = update_selected_drug_qty;

    vm.addSlide = function() {
      var newWidth = 600 + slides.length + 1;
      slides.push({
        image: img[slides.length % 6],
        id: currIndex++
      });
    };

    init();

    function init(){
        var fillCart = $localStorage.incomp_cart;
        vm.shopDetails = $localStorage['selectedShop'];
        // console.log(fillCart);
        var temp_data = $localStorage.log_status;
        if(!angular.isUndefined(temp_data)){
            // $rootScope.logStat = true;
            // vm.shop_detail = temp_data;
            if(angular.isDefined(fillCart)){
                vm.prefill = (fillCart[0].cart_type == 1) ? fillCart[0] : fillCart[1];
                if(vm.prefill.pharmacy == vm.shopDetails.id){
                    vm.selected_drug = processRawCart(vm.prefill.cartitems);
                }else{
                    $localStorage.incomp_cart = [];
                    $rootScope.isCartAdded = false;
                }
            }
        } else {
            openModel();
        }
        for (var i = 0; i < 4; i++) {
          vm.addSlide();
        }
    }

    function processRawCart(data){
        var modified = [];
        var temp_data = data.map( function(temp, index){
            modified[index] = {};
            angular.copy(temp, modified[index]);
            delete modified[index].product;
            modified[index].name = temp.product.name
            modified[index].product = temp.product.id;
        });
        return modified;
    }

    function search_drug(){
        if(vm.drug_key.length >= 3) {
            uploadService.getDrugInfo(vm.drug_key)
            .then(function(res) {
                if(res.results.length > 0) {
                    vm.drug_search_result = res.results;
                } else {
                    vm.drug_search_result = [];
                }
            });
        }else {
            vm.drug_search_result = [];
        }
    }

    function del_selected_drug(item) {
        var index = id(vm.selected_drug, item);
        vm.selected_drug.splice(index, 1);
    }

    function update_selected_drug_qty(action, item) {
        if(action && item.quantity <= 100){
            item.quantity++;
        } else {
            if(item.quantity <= 1){
                del_selected_drug(item);
            }else {
                item.quantity--;
            }
        }
    }

    function dataMakeOver(data){
        return {
            'product_type': data.type,
            'schedule': 'NA',
            'price': 0,
            'instruction': data.description,
            'quantity': 1,
            'size': 'M',
            'product': data.id,
            'name': data.name
        };
    }

    function add_drug(item){
        var temp_item = dataMakeOver(item);
        var index = id(vm.selected_drug, temp_item);
        if(index == -1) {
            vm.selected_drug.push(temp_item);
            vm.drug_search_result = '';
            vm.drug_key = undefined;
        } else {
            vm.drug_search_result = '';
            vm.drug_key = undefined;
        }
    }

    function id(list, item){
        if(list.length != 0){
            for(var i = 0; i < list.length; i++){
                if(list[i].product == item.product)
                    return i;
            }
            return -1;
        } else {
            return -1;
        }
    }

    function prepUpload() {
        if(vm.selectedImages.length <= 3) {
        	var file = vm.selectedImages;
            var reader = new FileReader();
            reader.readAsDataURL(file[0]);
            reader.onload = function () {
              vm.create_cart_presc.push(
                {
                    'user': $localStorage['usr'].usr_id,
                    'image': reader.result
                });
            };
            reader.onerror = function (error) {
              console.log('Error: ', error);
            };
        } else {
            vm.selectedImages.splice(-1, 1);
            alert('You can choose maximum 3 image for upload');
        }
    }

    function editSelected(index){
    	vm.selectedImages.splice(index, 1);
        vm.create_cart_presc.splice(index, 1);
    }

    function routeTo(flag) {
        if($rootScope.isCartAdded){
            console.log('update_cart');
        }else {
            console.log('create call');
            // create_cart();            
        }
        $localStorage['selectedDrug'] = vm.selected_drug;
    	$state.go('OTC');
    }

    function create_cart() {
        if(angular.isUndefined($localStorage['cart_id'])) {
            var cartID = utilities.genID($localStorage['usr'].usr_id + Date());
            var data = {
                'pharmacy': $localStorage['selectedShop'].id,
                'user': $localStorage['usr'].usr_id,
                'prescription': vm.create_cart_presc,
                'tot_presc_amt': 0,
                'tot_otc_amount': 0,
                'total_amount': 0,
                'cart_id': cartID,
                'cartitems': vm.selected_drug,
                'active': true,
                'cart_type': 1,
                'order_all': (vm.orderAll) ? 'A' : 'C'
            };

            uploadService.createCart(data)
                .then(function (res){
                    console.log(res);
                    $localStorage['cart'] = res;
                    $rootScope.isCartAdded = true;
                }, function(err){
                    alert(err);
                })
        }
    }

    function openModel() {
        var modalInstance = $uibModal.open({
              animation: true,
              backdrop: 'static',
              templateUrl: 'app/uploadPrescription/loginAlertTemp.html',
              controller: 'ModalInstanceCtrl',
              controllerAs: 'mod',
              size: 'lg'
            });

            modalInstance.result.then(function (selectedItem) {
                $state.go('Login');
            }, function () {
              // $log.info('Modal dismissed at: ' + new Date());
            });
    }

    imageListener = $scope.$watch(function () {
        return vm.selectedImages.length;
    }, function(newVal, oldVal) {
    	// console.log(newVal);
    	if(newVal === 0 || newVal === -1) {
    		vm.isImageSelected = false;
    	}else {
    		vm.isImageSelected = true;
    	}
    });
  }
})(angular);
(function(angular) {
  'use strict';

  angular
  .module('yoophawebapp')
  .controller('ModalInstanceCtrl', ModalInstanceCtrl);

    function ModalInstanceCtrl($uibModalInstance, $state, $localStorage, getData) {
        var mod = this;
        var url = ['http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/auth/register/', 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/auth/login/'];
        mod.showOtp = false;
        mod.inputs = {
          login_username: '',
          login_pwd: '',
          reg_username: '',
          reg_pwd: '',
          reg_mob: '',
          reg_email: '',
          reg_cpwd: '',
          otp: ''
        }
        mod.close = close;
        mod.status = '';

        mod.login = function (ev) {
          if(!angular.isUndefined(mod.inputs.login_username) && !angular.isUndefined(mod.inputs.login_pwd) && mod.inputs.login_username != '' && mod.inputs.login_pwd != ''){
              ev.preventDefault();
            var getURL = url[1];
            var data = {
              'mobile_no' : mod.inputs.login_username,
              'password' : mod.inputs.login_pwd
            }
            login(getURL, data);            
          }
        };

        mod.close = function (){
          $uibModalInstance.dismiss('cancel');
        }

        function login(url, data){
          getData.relQuery(url, data)
            .then(function(response) {
              if(response.status == 200 && response.statusText == 'OK'){
                var data = response.data;
                $localStorage['usr'] = 
                  {
                    'accessTok': data.token,
                    'usr_id': data.userid
                  }
                $localStorage['log_status'] = 1;
                $uibModalInstance.dismiss('cancel');
              }else {
                console.log(response);
              }
          });
        }

        mod.register = function (ev) {
          console.log(mod.inputs);
          if(!angular.isUndefined(mod.inputs.reg_username) && !angular.isUndefined(mod.inputs.reg_pwd) && mod.inputs.reg_username != '' && mod.inputs.reg_pwd != ''){
            // ev.preventDefault();
              var data = {
                'username': mod.inputs.reg_username,
                'confirm_password': mod.inputs.reg_cpwd,
                'password': mod.inputs.reg_pwd,
                'email': mod.inputs.reg_email,
                'mobile_no': mod.inputs.reg_mob
              }
                  getData.relQuery(url[0], data)
                    .then(function(response) {
                      if(response.status == 201 && response.statusText == 'Created'){
                          // clearText();
                          // $state.go('Login');
                          var data = {
                            'mobile_no' : mod.inputs.reg_mob,
                            'password' : mod.inputs.reg_pwd
                          }
                          login(url[1], data);
                      } else {
                        console.log('something went wrong try again later');
                      }
                  });
          }
        }
      }
})(angular);
