(function(angular) {
  'use strict';

  /**
   * @ngdoc service
   * @name medArun.getData
   * @description
   * # getData
   * Factory in the medArun.
   */
  angular.module('yoophawebapp')
    .factory('getData', getData);

  /** @ngInject */
  function getData($http, $log, $localStorage) {
    var myService = {
      async: function (url) {
        // url = 'json/sample.json';
        var promise = $http.get(url).then(function (response) {
          return response.data;
        });

        return promise;
      },

      asyncHead: function (url) {
        var promise = $http({
          method: 'GET',
          url: url,
          headers: {
            'Content-Type': 'application/json',
            'Authorization': 'JWT ' + $localStorage['usr'].accessTok
          }
        }).then(function (response) {
          return response;
        });

        return promise;
      },

      relQuery: function (url, sourceQuery) {
        var query = angular.copy(sourceQuery);
        // removeEmptyParams(query);
        $log.debug(angular.toJson(query));
        var promise = $http({
          method: 'POST',
          url: url,
          headers: {
            'Content-Type': 'application/json'
          },
          data: angular.toJson(query)
        }).then(function (response) {
          var output = angular.fromJson(angular.fromJson(response));
          // output.results.offset = query.offset;
          return output;
        });

        return promise;
      }
    };

    return myService;

    /**
     * Recursively removes empty parameters (objects||arrays) from container object
     * @param  {Object} obj Container object
     */
    function removeEmptyParams(obj) {
      var i;
      if (angular.isObject(obj)) {
        for (i in obj) {
          // because Object.keys(new Date()).length === 0;
          // we have to do some additional check
          if (Object.keys(obj[i]).length === 0 && (obj[i].constructor === Object || obj[i].constructor === Array)) {
            delete obj[i];
          } else {
            removeEmptyParams(obj[i]);
          }
        }
      }
    }
  }
})(angular);

