(function(angular) {
  'use strict';

  angular.module('yoophawebapp')
    .service('cartService', cartService);

  /** @ngInject */
  function cartService($q, $localStorage, getData, postData, utilities) {

    var service = {};
    service.createCustomer = createCustomer;
    service.getCustomerIfExist = getCustomerIfExist;
    service.getUserData = getUserData;
    service.createOrder = createOrder;
    service.getCartData = getCartData;

    function createOrder(url, data){
      var deferred = $q.defer();
      postData.async(url, data)
        .then(function(res) {
          if(res.status == 201 && res.statusText == 'Created')
            deferred.resolve(res.data);
          else 
            deferred.reject(res.data);
        });
      return deferred.promise;
    }

    function getCartData(url){
      var deferred = $q.defer();
      getData.asyncHead(url)
        .then(function(res) {
          deferred.resolve(res.data);
        });
      return deferred.promise;
    }

    function createCustomer(url, data){
      var deferred = $q.defer();
      postData.async(url, data)
        .then(function(res) {
          if(res.status == 201 && res.statusText == 'Created')
            deferred.resolve(res.data);
          else 
            deferred.reject(res.data);
        });
      return deferred.promise;
    }

    function getUserData(url){
      var deferred = $q.defer();
      getData.asyncHead(url)
        .then(function(res) {
          deferred.resolve(res.data);
        });
      return deferred.promise;
    }

    function getCustomerIfExist(userID){
      var url='http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/customer/customer/'+userID;
      var deferred = $q.defer();
      getData.asyncHead(url)
        .then(function(res) {
          if(res.data.results.length != 0)
            deferred.resolve(res.data.results);
          else 
            deferred.reject(res);
        });
      return deferred.promise; 
    }

    return service;

  }
})(angular);
(function(angular) {
  'use strict';

  angular
    .module('yoophawebapp')
    .controller('cartController', cartController);

  /** @ngInject */
  function cartController($localStorage, $uibModal, cartService, utilities) {
    var vm = this;
    var getUserDataUrl = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/common/users/';
    var getCartData = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/cart/cart/';
    vm.otcDetail = [];
    vm.customerDetails = {};
    vm.trash = trash;
    vm.changeQty = changeQty;
    vm.updateDrug = updateDrug;
    vm.createOrder = createOrder;
    vm.address = {
      'address_line1': '',
      'address_line2': '',
      'address_line3': '',
      'pincode': '',
      'district': 'Tamil Nadu-Chennai',
      'state': 'India-Tamil Nadu',
      'country': 'India',
      'address_type': 3
    }
    // angular.copy(vm.address, vm.shippingAddress);
    // angular.copy(vm.address, vm.billingAddress);

    init();

    function init(){
    	vm.otcDetail = $localStorage['selectedOTC'];
      vm.drugDetail = angular.isUndefined($localStorage['selectedDrug']) ? [] : $localStorage['selectedDrug'];
      cartService.getUserData(getUserDataUrl + $localStorage['usr'].usr_id)
        .then(function(res){
          $localStorage['usr'].email = res.email;
          $localStorage['usr'].mobile_no = res.mobile_no;
          $localStorage['usr'].username = res.username;
        })
        cartService.getCartData(getCartData + $localStorage['cart_id'].cart_id)
          .then(function (res) {
            console.log(res);
            vm.preFillDataCart = res;
          })
          isCustExistd();
    	// console.log(vm.otcDetail);
    }

    function updateDrug(item, flag){
      if(flag && item.quantity <= 100){
            item.quantity++;
            item.temprice = item.price * item.quantity;
        } else {
            if(item.quantity <= 1){
                del_selected_drug(item);
            }else {
                item.quantity--;
                item.temprice = item.temprice - item.price;
            }
        }
    }

    function del_selected_drug(item) {
      var index = id(vm.drugDetail, item);
      if(index != -1) {
        vm.drugDetail.splice(index, 1);
      }      
    }

    function id(list, item){
      if(list.length != 0){
            for(var i=0; i<list.length; i++){
                if(list[i].product === item.product)
                    return i;
                else 
                    return -1;
            }
        } else {
            return -1;
        }
    }

    function openAddressModel() {
      var modalInstance = $uibModal.open({
        animation: true,
        backdrop: 'static',
        templateUrl: 'app/cart/modelGetAddress.html',
        controller: 'ModalAddressInstanceCtrl',
        controllerAs: 'c_mod',
        size: 'lg'
      });

      modalInstance.result.then(function (selectedItem) {
          customerCreate();
      }, function () {
        // $log.info('Modal dismissed at: ' + new Date());
      });
    }

    function isCustExistd(){
      cartService.getCustomerIfExist($localStorage['usr'].usr_id)
        .then(function (res){
          $localStorage['cust'] = res;
          vm.customerDetails = res;
        }, function (res){
          vm.customerDetails = undefined;
          // cartService.createCustomer()
        })
    }

    function createOrder(){
      if(angular.isUndefined(vm.customerDetails)){
        openAddressModel();
      } else {
        cartService.createOrder(url, data)
          .then(function (res){

          })
      }
    }

    function trash(data){
    	var index = hasID(vm.otcDetail, data);
    	vm.otcDetail.splice(index, 1);
    	$localStorage['selectedOTC'] = vm.otcDetail;
    }

    function changeQty(action, item){
    	var index = hasID(vm.otcDetail, item);
    	if(action) {
    	  item.qty++
    	}else {
    	  if(item.qty == 0 || item.qty == 1){
    	    item.qty = 0;
    	    item.isAdded = false;
    	  }else if(item.qty > 1) {
    	    item.qty--;
    	  }
    	}
    	vm.otcDetail[index] = item;
    	$localStorage['selectedOTC'] = vm.otcDetail;
    }

    function hasID(list, item){
      if(!angular.isUndefined(list)){
        for(var i=0; i < list.length; i++){
          var element = list[i];
          if(item.productid == element.productid)
            return i;
        }
      }
      return -1;
    }

  }
})(angular);
