(function(angular) {
  'use strict';

  angular
    .module('yoophawebapp')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider, $locationProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('searchResult', {
        url: '/searchresult',
        templateUrl: 'app/searchresult/searchResult.html',
        controller: 'searchResultController',
        controllerAs: 'sr'
      })
      .state('Login',{
        url: '/login',
        templateUrl: 'app/login/login.html',
        controller: 'loginController',
        controllerAs: 'login'
      })
      .state('uploadPrescription', {
        url: '/upload',
        templateUrl: 'app/uploadPrescription/uploadPrescription.html',
        controller: 'uploadPrescriptionController',
        controllerAs: 'upload'
      })
      .state('OTC', {
        url: '/otc',
        templateUrl: 'app/otc/otc.html',
        controller: 'otcController',
        controllerAs: 'otc'
      })
      .state('Cart', {
        url: '/cart',
        templateUrl: 'app/cart/cart.html',
        controller: 'cartController',
        controllerAs: 'cart'
      })
      .state('Payment', {
        url: '/payment',
        templateUrl: 'app/payment/payment.html',
        controller: 'PaymentController',
        controllerAs: 'payment'
      })
      .state('Register', {
        url: '/register',
        templateUrl: 'app/register/register.html',
        controller: 'RegisterController',
        controllerAs: 'register'
      });

    $urlRouterProvider.otherwise('/');
    $locationProvider.html5Mode(true);
  }

})(angular);


